import { ProjectReportId, ReportText, Title } from '../value';

import {
  BaseProjectReport,
  BaseUpdateProjectReport,
  IBaseProjectReport,
  IUpdateBaseProjectReport,
} from './BaseProjectReport';

export interface IProjectCommonReport extends IBaseProjectReport {
  title: Title;
  reportText: ReportText;
}
export interface IUpdateProjectCommonReport extends IUpdateBaseProjectReport {
  title?: Title;
  reportText?: ReportText;
}

export class ProjectCommonReport extends BaseProjectReport {
  readonly title: Title;
  readonly reportText: ReportText;

  constructor(args: IProjectCommonReport) {
    super(args);
    this.title = args.title;
    this.reportText = args.reportText;
  }

  update(args: IUpdateProjectCommonReport): UpdateProjectCommonReport {
    return new UpdateProjectCommonReport({ id: this.id, ...args });
  }
}

export class UpdateProjectCommonReport extends BaseUpdateProjectReport {
  readonly title?: Title;
  readonly reportText?: ReportText;

  constructor(args: IUpdateProjectCommonReport & { id: ProjectReportId }) {
    super(args);
    this.title = args.title;
    this.reportText = args.reportText;
  }
}
